.wrapper{
	user-select:none;
	position:relative;
	//height:100vh;
	//min-width:850px;
	color:white;
	.bg-img{
		position: absolute;
	    width: 100%;
	    z-index: -99;
	    height: 100%;
		background-image: url('images/bg.jpg');
		background-attachment: fixed;
		background-position: center;
		bacground-repeat:no-repeat;
		filter: blur(3px);
		background-size:cover;
		//background-color:black;
	}
	.default-content{
		padding:100px;
		//position:absolute;
		//top:50%;
		//left:50%;
		//width:100%;
		//min-width:800px;
	    //-ms-transform: translate(-50%, -50%);
		//transform: translate(-50%,-50%);
		//border:10px solid pink;
		//display:grid;
		grid-template-rows:1fr 1fr;

		.header-content{
			display:grid;
			grid-template-columns: 1fr 1fr 1fr;
			//border:2px solid gold;
			grid-auto-rows:minmax(250px, 250px);
			.name{
				padding:1rem;
				font-size:5rem;
				justify-self:center;
				align-self:center;
				.first-name{

				}
				.second-name{

				}
			}
			.skill-choice-wrapper{
				justify-self:center;
				align-self:center;
				width:300px;
			}
			.skill-choices{
				align-self:center;
				padding:1rem;
				//border:2px solid green;
				//height:50px;
				overflow:hidden;
				position:relative;
				//font-size:3rem;
				text-align:center;

				.skill-choice{
					//margin:15px;
					font-size:3rem;
					color:#2d2d2d;
				}
			}
			.word-wrapper{
				margin-left:1rem;
				align-self:center;
				font-size:5rem;
				justify-self:start;
			}
		}
		.skill-display{
			padding:1rem 7rem;
			.display-title{
				position:relative;
				display:grid;
				.display-name{
					//justify-self:center;
				}
				.display-value{
					//justify-self:center;
				}
				.display-arrow{
					position:relative;
					display:inline-block;
					top:-30px;
					left:0px;
					transform:rotate(90deg);
  					transform-origin: bottom left;
				}
			}
			.display-content{
					background-color:rgba(0,0,0,0.5);
				display:grid;
				grid-template-columns:1fr 1fr 1fr;
				.framework{
					justify-self:center;
					margin:1rem;
				}
			}
			.spacefiller{
				display:grid;
				background-color:rgba(0,0,0,0.5);
				color:white;
				justify-content:center;
				align-content:center;
				min-height:200px;
				content:'';
			}
		}
		.projects-container{
			padding:1rem 7rem;
			position:relative;
			margin-top:2rem;
			.display-title{
				position:relative;
				.project-header{
					margin-bottom:2rem;
				}
				.display-arrow{
					display:inline-block;
					position:relative;
					top:-30px;
					//left:170px;
					transform:rotate(90deg);
					//transform: rotate(360deg);
  					transform-origin: bottom left;
				}
			}
			.display-content{
				display:grid;
				grid-template-columns:1fr 10fr 1fr;
				background-color:rgba(0,0,0,0.5);
				.project-container{
					margin-top:1rem;
					position:relative;
					justify-self:center;
					.project-title{
						font-size:3rem;
					}
					.project-img{
						text-align:center;
						img{
							width:100%;
							height:600px;
							object-fit:contain;
						}
					}
				}
				.navPrevProject,.navNextProject{
					align-self:center;
				}
			}
			.spacefiller{
				display:grid;
				background-color:rgba(0,0,0,0.5);
				color:white;
				justify-content:center;
				align-content:center;
				min-height:200px;
				content:'';
			}
		}
	}
}
.center{
	text-align:center;
}
.nav-up,.nav-down,.navPrevProject,.navNextProject{
	cursor:pointer;
}
.display-name,.project-header{
	font-size:2rem;
}

@media only screen and (max-width:1020px){
	.wrapper{
		margin:0;
		.default-content{
			width:auto;
			
			.header-content{
				grid-template-columns:1fr;
				.name{
					.first-name{

					}
					.second-name{

					}
				}
				.skill-choice{

				}
				.word-wrapper{
					justify-self:center;
				}
			}
			.skill-display{
			
			}
		}
	}
}